.login {
  width: 27em;
  height: 40em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  position: relative;
  z-index: 99; }
  .login .container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
    border-radius: 4px;
    position: relative;
    z-index: 99;
    width: 100%;
    height: 100%;
    z-index: 99;
    padding: 17px 10px; }
  .login .right-side {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 90%;
    background-color: #3498db;
    width: 100%;
    position: absolute;
    right: -34%;
    border-radius: 6px;
    z-index: 1;
    transition: all 400ms ease-in-out;
    cursor: pointer;
    box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281); }
    .login .right-side.right {
      right: -40%;
      align-items: flex-end; }
      .login .right-side.right:hover {
        right: -45%; }
    .login .right-side.left {
      right: 40%;
      align-items: flex-start; }
      .login .right-side.left:hover {
        right: 45%; }
    .login .right-side .text {
      font-size: 21px;
      font-weight: 500;
      color: #fff;
      margin-right: 3em;
      margin-left: 3em; }

.btn {
  font-size: 15px;
  padding: 5px 20px;
  border: 0;
  background-color: #2b7aaf;
  color: #fff;
  border-radius: 3px;
  transition: all 250ms ease-in-out;
  cursor: pointer; }
  .btn:hover {
    background-color: #2386c8; }
  .btn:focus {
    outline: none; }

.base-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .base-container .header {
    font-size: 24px;
    font-family: "Open Sans", sans-serif; }
  .base-container .content {
    display: flex;
    flex-direction: column; }
    .base-container .content .image {
      width: 21em; }
      .base-container .content .image img {
        width: 100%;
        height: 100%; }
    .base-container .content .form {
      margin-top: 2em;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .base-container .content .form .form-group {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content; }
        .base-container .content .form .form-group label {
          font-size: 20px; }
        .base-container .content .form .form-group input {
          margin-top: 6px;
          min-width: 18em;
          height: 37px;
          padding: 0px 10px;
          font-size: 16px;
          font-family: "Open Sans", sans-serif;
          background-color: #f3f3f3;
          border: 0;
          border-radius: 4px;
          margin-bottom: 31px;
          transition: all 250ms ease-in-out; }
          .base-container .content .form .form-group input:focus {
            outline: none;
            box-shadow: 0px 0px 12px 0.8px #3474dbb2; }
  .base-container .footer {
    margin-top: 3em; }
  .base-container .errorMessage {
    color: red; }

.password {
  display: block;
  position: relative;
  text-transform: uppercase;
  font-weight: 700;
  width: 100%;
  color: #727272; }
  .password__input {
    display: block;
    text-transform: none;
    width: 100%;
    height: 42px;
    border-width: 0 0 1px;
    border-style: solid;
    border-color: #B6B6B6;
    font-weight: 400;
    color: #212121; }
    .password__input:focus, .password__input:active {
      border-color: #00BCD4;
      outline: 0; }
  .password__show {
    cursor: pointer;
    position: absolute;
    bottom: 11px;
    height: 16px;
    right: 0;
    background: #727272;
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-weight: 700;
    font-size: .8em; }
  .password__strength {
    position: absolute;
    width: 0%;
    height: 4px;
    bottom: 170px;
    left: 0;
    background: transparent;
    transition: all 300ms ease-in-out; }
    .password__strength[data-score="null"] {
      width: 0;
      background: red; }
    .password__strength[data-score="0"] {
      width: 5%;
      background: #F44336; }
    .password__strength[data-score="1"] {
      width: 25%;
      background: #F44336; }
    .password__strength[data-score="2"] {
      width: 50%;
      background: #FFEB3B; }
    .password__strength[data-score="3"] {
      width: 75%;
      background: #4CAF50; }
    .password__strength[data-score="4"] {
      width: 100%;
      background: #4CAF50; }

